import React, { Component } from 'react';
import Card, { CardSection,CardHeader} from "@kiwicom/orbit-components/lib/Card";
import Airplane from '@kiwicom/orbit-components/es/icons/Airplane';
export class StatsCard extends Component {
  render() {
    return (
      <div className="card">
        <Card>
          <CardHeader icon={<Airplane />} title="Card with title &amp; description" subTitle="This is a description of the card." />
        </Card>
      </div>
    );
  }
}

export default StatsCard;



